@import url(https://fonts.googleapis.com/css?family=Montserrat|Ubuntu&display=swap);
.App {
  min-height: 100vh;
  height: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.filmpage-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #a05c5cb7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 999;
  color: #fafafa;
}

.filmpage {
  margin: 10px;
  background-color: #323232;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  height: 350px;
  max-width: 800px;
}

.title {
  text-align: center;
  font-size: 130%;
}

.film-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 25px;
  height: 90%;
}

.poster {
  height: 350px;
}

.poster img {
  max-height: 100%;
  border-radius: 25px 0 0 25px;
}

.description {
  overflow-y: auto;
  margin: 10px 0;
}

.extra-info {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.close-but {
  position: fixed;
  top: 30px;
  right: 30px;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.close-but:hover {
  background-color: rgb(204, 204, 204, 0.5);
}

.close-but svg {
  fill: #d3d3d3;
}

@media screen and (max-width: 550px) {
  .filmpage {
    flex-direction: column;
    height: 100%;
    overflow-y: auto;
  }

  .poster {
    margin-top: 20px;
    height: 250px;
  }

  .poster img {
    max-height: 100%;
    border-radius: 25px;
  }
}

.filters-wrapper {
  position: fixed;
  bottom: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.filters {
  width: auto;
  border: 5px solid #782629;
  background-color: #9c1d25da;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

@media screen and (max-width: 550px) {
  .filters {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

.filters .left {
  padding: 30px 30px 0 30px;
}
.filters .right {
  padding: 10px;
}

.open {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px 25px 0 0;
  background-color: #782629;
}

.open button {
  padding: 10px 0;
  color: #fafafa;
  width: 100%;
}

.rating {
  margin-bottom: 60px;
}

.dates {
  margin-bottom: 40px;
}

.input-range__track--active {
  background: #741010;
}

.input-range__slider {
  background: #741010;
  border-color: #741010;
}

.input-range__label--value {
  color: #fafafa;
  padding-bottom: 10px;
}

.input-range__label--max,
.input-range__label--min {
  color: #fafafa;
}

.input-range__label--value span {
  background: #741010;
  padding: 1px 4px;
  border-radius: 5px;
}

.genres-wrapper {
  font-size: 13px;
}

ul.genres {
  list-style: none;
  text-align: center;
  padding: 0;
  margin: 0;
}
ul.genres li {
  display: inline;
  margin-right: 5px;
}
ul.genres li label {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.9);
  color: #696969;
  border-radius: 25px;
  white-space: nowrap;
  margin: 3px 0px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

ul.genres li label {
  padding: 8px 12px;
  cursor: pointer;
  font-family: 'Ubuntu', sans-serif;
}

ul.genres li label::before {
  display: inline-block;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 12px;
  padding: 2px 6px 2px 2px;
  content: '\f067';
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

ul.genres li input[type='checkbox']:checked + label::before {
  content: '\f00c';
  -webkit-transform: rotate(-360deg);
          transform: rotate(-360deg);
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

ul.genres li input[type='checkbox']:checked + label {
  background-color: #741010;
  color: #fafafa;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

ul.genres li input[type='checkbox'] {
  display: absolute;
}
ul.genres li input[type='checkbox'] {
  position: absolute;
  opacity: 0;
}

.certification {
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  margin: 30px auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.certification > input {
  display: none;
}
.certification span {
  color: #fafafa;
  height: 100%;
  margin: auto 5px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.certification i {
  display: inline-block;
  width: 49px;
  height: 25px;
  border-radius: 20px;
  vertical-align: middle;
  -webkit-transition: 0.25s 0.09s;
  transition: 0.25s 0.09s;
  position: relative;
  background-color: rgba(255, 255, 255, 0.9);
}
.certification i:after {
  content: ' ';
  display: block;
  width: 15px;
  height: 15px;
  top: 5px;
  left: 5px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.4);
  -webkit-transition: 0.15s;
  transition: 0.15s;
}
.certification > input:checked + i {
  background: #741010;
}
.certification > input:checked + i + span {
  color: #741010;
}
.certification > input:checked + i:after {
  -webkit-transform: translateX(25px);
  transform: translateX(25px);
}

.lds-heart {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}
.lds-heart div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: #741010;
  -webkit-animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
          animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}
.lds-heart div:after,
.lds-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: #741010;
}
.lds-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}
.lds-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}
@-webkit-keyframes lds-heart {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
@keyframes lds-heart {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

.movie {
  height: 250px;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.movie button {
  height: 100%;
  border-radius: 15px;
}

.movie button:hover .hover {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

.hover {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  border-radius: 15px;
}

.movie img {
  border-radius: 15px;
  max-width: 100%;
  height: 100%;
}
.random-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 340px;
}

.random-wrapper > button {
  font-size: 200%;
  margin: 10px;
}

.flip-card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 60px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transform: 0.8s;
          transform: 0.8s;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

@-webkit-keyframes flipping {
  from {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  to {
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
  }
}

@keyframes flipping {
  from {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  to {
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
  }
}

.flipping {
  -webkit-animation: flipping 0.7s linear infinite;
          animation: flipping 0.7s linear infinite;
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flip-card-front {
  background-color: #741010;
  border-radius: 20px;
}

.flip-card-back {
  background-color: #741010;
  border-radius: 20px;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.error{
  background-color: #741010a4;
  border-radius: 25px;
  color: #fafafa;
  padding: 20px;
  text-align: center;
  font-size: 1.5em;
}

.weeksHot-wrapper {
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(58, 44, 44, 0.5);
  border-radius: 0 0 25px 25px;
  /* height: 69px; */
  -webkit-transition: 2s;
  transition: 2s;
}

.week-title {
  display: flex;
  justify-content: center;
  color: #fafafa;
  font-family: 'Montserrat', sans-serif;
}

.week-title svg {
  margin-left: 10px;
  fill: #fafafa;
}

.carousel {
  width: 100vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.carousel-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.carousel button {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.carousel__slider {
  height: 250px;
  margin-bottom: 10px;
}

.carousel__back-button,
.carousel__next-button {
  margin: 0 10px;
  border-radius: 50%;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  height: 35px;
  width: 35px;
  cursor: pointer;
  fill: #fafafa;
}

.carousel__back-button:hover,
.carousel__next-button:hover {
  background-color: rgb(204, 204, 204, 0.5);
}

#particles-js > div {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1;
}

#particles-js canvas {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

